import React, { Fragment} from "react"
import {Link , NavLink} from "react-router-dom"
import SignUp from "./Sign up"

import "./Nav.css";

import Logo from "./logo.png"
class Nav extends React.Component{
   

   
    
  render(){

   
   return (
        
        <Fragment>
    
        <td className="NavTableTD" ><img id="logo" src={Logo}/></td> 
        <td onClick={()=> this.props.HC()} className="NavTableTD"> <NavLink id="HomePage_title" className="NavTabeleTD" activeClassName="clicked" to="/Startsite">Home Page</NavLink></td> 
        <td  onClick={()=> this.props.HC()} className="NavTableTD"> <NavLink className="NavTabeleTD"  activeClassName="clicked" to="/Hero">Hero</NavLink></td>
        <td onClick={()=> this.props.HC()} className="NavTableTD"><NavLink  className="NavTabeleTD"activeClassName="clicked" to="/News">News</NavLink></td>
        <td onClick={()=> this.props.HC()} className="NavTableTD"> <NavLink className="NavTabeleTD" activeClassName="clicked" to="/DLC">DLC</NavLink></td>
        <td onClick={()=> this.props.HC()} className="NavTableTD"> <NavLink className="NavTabeleTD" activeClassName="clicked" to="/Sign up">Sign up</NavLink></td>

      
        
        



     
            </Fragment>
           
         

        
        
    )} 
   
}
// <NavLink activeClassName="clicked" to="/Hero"> Hero</NavLink>
// <NavLink activeClassName="clicked" to="/News">News</NavLink>
// <NavLink activeClassName="clicked" to="/OurTeam">OurTeam</NavLink>
// <NavLink activeClassName="clicked" to="/Startsite">Contact</NavLink>
export default Nav